<template>
  <div :class="`navigation ${min ? 'min' : ''} ${hide ? 'hide' : ''}`">
    <div class="layout-container-fluid nav-container">
      <div class="layout-container nav-container">
        <div class="nav">
          <img class="logo" src="@/assets/img/nav-logo.png" alt @click="$router.push({path: '/'})" />
          <!-- <div
            class="menu"
            @click="toggleSidebar"
            v-show="!isOpen"
          >
            <font-awesome-icon icon="bars" />
          </div> -->
          <div :class="`mask ${isOpen ? 'open' : ''}`">
            <div class="close" @click="isOpen = false">
              <img src="../assets/img/close.png" alt="">
            </div>
          </div>
          <ul :class="`navlist ${isOpen ? 'open': ''}`">
            <li v-show="item.link" class="flex-ac" v-for="item in list" :key="item.name" @click="enterPage(item)">
              <span class="link">
                <span>
                  <div class="title">{{item.name}}</div>
                </span>
              </span>
            </li>
            <a :href="info.fbLink" target="_blank" class="social-link" v-if="info.fbLink">
              <img src="../projects/shin-lan/shin-lan/all/fb.png" alt="">
            </a>
            <a :href="info.lineLink" target="_blank" class="social-link" v-if="info.lineLink">
              <img src="../projects/shin-lan/shin-lan/all/line.png" alt="">
            </a>
            <li class="flex-ac" @click="$router.push('/login')" v-if="!$store.getters['user/isLogin']">
              <span class="link">
                <span>
                  <div class="title">登入</div>
                </span>
              </span>
            </li>
            <li class="flex-ac" @click="$store.dispatch('user/logout')" v-else>
              <span class="link">
                <span>
                  <div class="title">登出</div>
                </span>
              </span>
            </li>
          </ul>
        </div>
      </div>
    </div>
    <el-dialog title :visible.sync="isShowHintDialog" :width="isMobile ? '90%' : '90%'" :modal-append-to-body="false">
      <LoginHint />
    </el-dialog>
  </div>
</template>

<script>
import { isMobile, isTablet } from '@/utils'
import navList from '@/info/navList'
import info from '@/info'
import LoginHint from '@/components/LoginHint.vue'

export default {
  name: 'navigation',
  components: {
    LoginHint,
  },
  data() {
    return {
      isOpen: false,
      isMobile,
      isTablet,
      list: navList,
      info,
      isShowHintDialog: false,
    }
  },

  props: ['min', 'hide'],

  computed: {
    offset() {
      if (this.isMobile) {
        return -39
      }

      if (this.isTablet) {
        return -45
      }

      return -56
    },
  },

  methods: {
    toggleSidebar() {
      this.isOpen = !this.isOpen
    },

    enterPage(item) {
      if (!this.$store.getters['user/isLogin'] && item.link == '/member') {
        this.isShowHintDialog = true
      } else {
        this.$router.push({ path: item.link })
      }
    },
  },
}
</script>
